module.exports = function () {

    //TODO::replace the image.
    let sharePng = "data:image/png;base64,";

    let newrecordPng = "data:image/png;base64,";

    let plugin =  cc.Class({
        extends:cc.Object,

        //intent  "INVITE" | "REQUEST" | "CHALLENGE" | "SHARE"
        shareFb:function (intent = 'REQUEST',callback) {

            FBInstant.shareAsync({
                intent: intent,
                image: sharePng,
                text: "It's a amazing game, play with me!",
                data: { myReplayData: '...' },
            }).then(function() {
                // continue with the game.
                if(callback)
                {
                    callback();
                }
            });
        },

        shareNewRecord:function (score,callback) {
            FBInstant.shareAsync({
                intent: "REQUEST",
                image: newrecordPng,
                text: 'I got new record ' + score.toString(),
                data: { myReplayData: '...' },
            }).then(function() {
                // continue with the game.
                if(callback)
                {
                    callback();
                }
            });
        },

        playerName:function () {
            var playerName = FBInstant.player.getName();

            return playerName;
        },

        chooseAsync:function (callback) {
            FBInstant.context
                .chooseAsync()
                .then(function() {
                    console.log(FBInstant.context.getID());
                    if(callback){
                        callback(FBInstant.context.getID());
                    }
                });
        },

        updateAsync:function (text,callback) {
            FBInstant.updateAsync({
                action: 'CUSTOM',
                cta: 'Play',
                image: sharePng,
                text: {
                    default: FBInstant.player.getName() + text,
                },
                template: 'play_turn',
                data: { myReplayData: '...' },
                strategy: 'IMMEDIATE',
                notification: 'NO_PUSH',
            }).then(function() {
                console.log('Message was sent successfully');
                if(callback){
                    callback();
                }
            });
        },

        updateHighAsync:function (text,callback) {
            FBInstant.updateAsync({
                action: 'CUSTOM',
                cta: 'Play',
                image: newrecordPng,
                text: {
                    default: FBInstant.player.getName() + text,
                },
                template: 'play_turn',
                data: { myReplayData: '...' },
                strategy: 'IMMEDIATE',
                notification: 'NO_PUSH',
            }).then(function() {
                console.log('Message was sent successfully');
                if(callback){
                    callback();
                }
            });
        },

        createShortCut:function () {
            FBInstant.canCreateShortcutAsync()
                .then(function(canCreateShortcut) {
                    if (canCreateShortcut) {
                        FBInstant.createShortcutAsync()
                            .then(function() {
                                // Shortcut created
                            })
                            .catch(function() {
                                // Shortcut not created
                            });
                    }
                });
        },

        InterstitialAdAsync:function (callback) {
            let GameConfig = require("GameConfig");
            var ad = null;

            FBInstant.getInterstitialAdAsync(
                GameConfig.InterstitialAdId,
            ).then(function(interstitial) {
                console.log("ad loadAsync:");
                // showlb.string = "ad loadAsync:";
                ad =  interstitial;
                return interstitial.loadAsync();
            }).then(function() {
                // Ad loaded
                // showlb.string = "ad loadAsync:";
                console.log("Ad loaded");
                return ad.showAsync();
            }).then(function () {
                // showlb.string = "I don't know what happened";
                console.log("I don't know what happened");
                if (callback) {
                    callback();
                }
            });
        },


        RewardedVideoAsync:function (callback) {
            let GameConfig = require("GameConfig");
            var ad = null;
            FBInstant.getRewardedVideoAsync(
                GameConfig.RewardedVideoId,
            ).then(function(rewardedVideo) {
                ad = rewardedVideo;
                return ad.loadAsync();
            }).then(function() {
                // Ad loaded
                console.log("Ad loaded");
                return ad.showAsync();
            }).then(function () {
                console.log("I don't know what happened");
                if (callback) {
                    callback();
                }
            });


            // try {

            // FBInstant.getRewardedVideoAsync(
            //     GameConfig.RewardedVideoId,
            // ).then(function (rewardedVideo) {
            //     console.log("rewardedVideo ad id:", rewardedVideo.getPlacementID());
            //     ; // 'my_placement_id'
            //
            //     if (callback) {
            //         callback();
            //     }
            // }).catch(function(e){
            //     console.log(e,e.message, e.name);
            // });
            // }
            // catch(e){
            //     console.log(e,e.message, e.name); // 传递异常对象到错误处理
            // }
        }
        ,

    });


    let p = new plugin();
    return p;

}();